<template>
	<div class="app-container" v-loading="isLoad" >
		<el-form :label-position="labelPosition" label-width="80px" :model="form" ref='form' style="width:500px ;margin:0 auto;" :rules="rules">
			<el-form-item label="选择角色:">
				<el-select v-model="form.id" filterable placeholder="请选择" @change="companyChange" style="width: 100%;" :disabled="isSaveClick">
					<el-option v-for="item in itemList" :key="item.mid?item.mid:item.ctId" :label="item.mname?item.mname:item.ctName" :value="item.mid?item.mid:item.ctId">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="ICCID:" prop="iccid"  v-if="isEmpty(form.discount)">
				<el-input v-model="form.iccid"  placeholder="请输入内容"></el-input>
			</el-form-item>
			<el-form-item label="自管理门户账号:"prop="account">
				<el-input v-model="form.account" :disabled="isdisabled" placeholder="请输入内容"></el-input>
			</el-form-item>
			<el-form-item label="自管理门户密码:" prop="password">
				<el-input v-model="form.password" :disabled="isdisabled" placeholder="请输入内容"></el-input>
			</el-form-item>
			<el-form-item label="电信API接口密码:" prop="key">
				<el-input v-model="form.key" :disabled="isdisabled" placeholder="请输入内容"></el-input>
			</el-form-item>
			<el-form-item label="折扣(小数点两位):" prop="discount">
				<el-input v-model="form.discount" :disabled="isdisabled" placeholder="请输入内容"></el-input>
			</el-form-item>
			<el-form-item>
				<el-button @click="saveAction" v-if="!isdisabled" :disabled="isSaveClick">保存</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
/* eslint-disable quotes,indent,keyword-spacing,spaced-comment,no-var */
import {
	giveManufacturerAuthority,
	getListManufacturerGeneral,
} from '../../../api/admin/usual';
import {
	getManufacturerInfoByLogin,
	updateManufacturerKey
} from '../../../api/admin/basicData';
import { backCN } from 'utils/index';
 import { mapActions } from 'vuex'
// import { setLoad } from 'store/modules/loading'
export default {
	data() {
		return {
			labelPosition: 'left',
			itemList: [],
			form: {
				id: '',
				iccid:'',
				account: '',
				password: '',
				key: '',
				discount: ''
				
			},
			rules: {
				iccid:[{ required: true, message: 'ICCID不能为空' }],
				account: [{ required: true, message: '自管理门户账号不能为空' }],
				password: [{ required: true, message: '自管理门户密码不能为空' }],
				key: [{ required: true, message: '电信API接口密码不能为空' }],
				discount: [{ required: true, message: '折扣不能为空' }]
			},
			isdisabled :true,
			isSaveClick:false,
			cur_mid: 0
		}
	},
	computed:{
		isLoad(){
			return this.$store.state.loading.load
		}
	},
	watch: {},
	mounted() {
		this.listManufacturerGeneral();
	},
	methods: {
		...mapActions(["setLoad"]),
		saveAction(){
			this.$refs['form'].validate((valid) => {
				if (valid) {
					let account = this.form.account
					let iccid=this.form.iccid
					let password = this.form.password
					let key = this.form.key
					let discount=this.form.discount
					this.isLoad=true
					this.isSaveClick=true;
					updateManufacturerKey({ account,iccid, password,key, discount }).then(res => {
						this.isdisabled = true
						res.msg === 0 ? this.$message.success("保存成功！") : this.$message.error(backCN(res.code))
                        res.msg===0?'':this.isLoad=false
					}).catch(()=>{
						this.isdisabled=true
					})
				} else {
					return false;
				}
			});
		},
		isEmpty(val) {
			if (val === '' || val === undefined) {
				return true
			} else {
				return false
			};
		},
		companyChange(val) {
			this.manufacturerInfoByLogin(val);
		},
		manufacturerInfoByLogin(mid) {
				this.isLoad=true
			giveManufacturerAuthority({
				mid: mid
			})
				.then(res => {
						this.isLoad=false
					if (res.msg == 0) {
							this.isLoad=true
						getManufacturerInfoByLogin()
							.then(res => {
							this.isLoad=false
								if (res.msg == 0) {
									this.form.account = res.account;
									this.form.password = res.password;
									this.form.key = res.key;
									this.form.discount = res.discount;
									res.discount?this.isdisabled=true:this.isdisabled=false
								}
							}).catch(()=>{
						this.isdisabled=true
					});
					}
				}).catch(()=>{
						this.isdisabled=true
					});
		},
		listManufacturerGeneral() {
		this.isLoad=true
			getListManufacturerGeneral()
				.then(res => {
					this.isLoad=false
					if (res.msg == 0) {
						this.itemList = res.mList;
						this.form.id= res.mList[0].mid;
						this.cur_mid = res.mList[0].mid;
						this.manufacturerInfoByLogin(this.cur_mid);
					}
				}).catch(()=>{
						this.isdisabled=true
					});
		}
	}
};
</script>

<style scoped>
.app-container {
	background: #f5f5f5;
}

.item-left {
	padding: 7px 0;
}
</style>


